* {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
  margin: auto;
  scroll-behavior: smooth;
}

.nav-links {
  background-color: cadetblue;
  overflow: hidden;
  padding: 10px;
}

.burger-nav {
  height: 0px;
}

.burger-nav-visible {
  height: 150px;
  transition: height 1s ease 0s;
}

.burger-p-visible {
  visibility: visible;
}

.burger-nav ul {
  background-color: cadetblue;
  padding-bottom: 5px;
  padding-top: 40px;
}

.burger-p {
  background-color: inherit;
  list-style: none;
  padding: 5px;
  overflow: hidden;
}

.burger-nav ul :hover {
  background-color: rgb(74, 114, 116);
  cursor: pointer;
}

.fontAwesomeIconBar {
  background: inherit;
  background-color: cadetblue;
  float: left;
  height: auto;
  font-size: 16pt;
}

.fontAwesomeIconBar :hover {
  cursor: pointer;
}

.topBtn {
  width: 30px;
  height: 30px;
  border-radius: 50px;
  position: fixed;
  bottom: 8px;
  background-color: cadetblue;
  opacity: 0.8;
  right: 8px;
}

.topBtn:hover {
  opacity: 1;
  cursor: pointer;
}

.fontAwesomeIcon {
  background: none;
  padding: 5px;
}

.fontAwesomeIcon :hover {
  cursor: pointer;
}

.about-me p {
  background-color: white;
  padding: 30px;
  border-radius: 20px;
  width: 80%;
  margin-top: 20px;
  margin-bottom: 30px;
}

.about-me h1 {
  margin: 30px 0 30px 0;
}

.about-me img {
  width: 200px;
  height: 200px;
  border-radius: 60px;
}

.language ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 250px;
  padding: 20px;
}

.language li {
  padding: 10px;
}

.language-icons {
  font-size: 32pt;
  opacity: 0.5;
  padding-top: 10px;
  padding-right: 10px;
}

.language li :hover {
  color: cadetblue;
  opacity: 1;
  cursor: pointer;
}

.container {
  position: relative;
  text-align: center;
  width: 300px;
}

.centered p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  font-size: 37pt;
  color: cadetblue;
  cursor: pointer;
}

.cases a:hover + .centered p {
  color: transparent;
}

.cases h1 {
  margin: 30px 0 30px 0;
}

.cases h4 {
  font-size: 17pt;
  padding: 10px 0 30px 0;
}

.cases img {
  width: 200px;
  height: 150px;
  border-radius: 50%;
  transition: all 800ms;
  filter: blur(5px);
}

.cases a :hover {
  border-radius: 10%;
  filter: none;
}

@media screen and (min-width: 992px) {
  .fontAwesomeIconBar {
    display: none;
  }

  .burger-nav {
    background-color: cadetblue;
    height: 50px;
  }

  .burger-nav li {
    display: block;
    padding: 15px;
    border-radius: 25px;
    width: 100%;
  }

  .burger-nav ul {
    display: flex;
    flex-direction: row;
    padding: 0;
  }

  .topBtn {
    width: 50px;
    height: 50px;
  }

  .fontAwesomeIcon {
    font-size: 22pt;
    padding-top: 10px;
  }

  .nav-links {
    overflow: visible;
  }

  .about-me p {
    width: 40%;
    margin-bottom: 30px;
  }

  .about-me h1 {
    margin: 30px 0 30px 0;
  }

  .language ul {
    justify-content: space-between;
    margin-bottom: 30px;
    width: 35%;
  }

  .language ul li {
    margin: 20px;
    padding-right: 20px;
  }

  .label-icon p {
    padding-right: 20px;
  }

  .cv {
    margin-bottom: 30px;
  }

  .cases-body {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 70%;
  }

  .cases h1 {
    margin-bottom: 30px;
  }
}
